<template>
  <div>
    <div id="container" :style="width">
    </div>
    <el-tooltip class="item" effect="dark" content="" placement="top">
      <el-button class="roadConditionBtn" circle icon="el-icon-place" @click="openRoadCondition"></el-button>
    </el-tooltip>
    <div class="search">
      <transition name="el-fade-in" mode="in-out">
        <Search road-text="" :search="false" v-show="state1" :icon="'el-icon-search'" :flag="4" :get-location="true"></Search>
      </transition>
      <el-collapse-transition>
        <div v-show="state2">
          <search road-text="" :search="false" :flag="1" :icon="'el-icon-delete'" :get-location="true"></search>
          <search road-text="" :search="false" :flag="2" :icon="'el-icon-delete'" :get-location="true"></search>
          <div v-show="checkRoute">
            <el-button class="route-button" round size="mini" :loading="loading" @click="showDrawer"></el-button>
          </div>
        </div>
      </el-collapse-transition>
      <transition name="el-zoom-in-bottom">
        <span :style="styleObj" v-show="state1"><el-button class="el-icon-refresh" circle @click="changeState"></el-button></span>
      </transition>
      <transition name="el-fade-in">
        <span :style="styleObj" v-show="state2"><el-button class="el-icon-refresh" circle @click="changeState"></el-button></span>
      </transition>
    </div>
  </div>
</template>

<script>
import Search from "@/components/Search";
import {mapState, mapActions} from 'vuex'
export default {
  name: "BMap",
  components: {Search},
  data() {
    return {
      trafficOn: false,
      keyword: '',
      selection: '',
      width: '1305px;',
      map: '',
      state1: true,
      state2: false,
      styleObj: {
        marginLeft: '5px',
        paddingTop: '0'
      },
      destination: [],
      checkRoute: false,
      loading: true
    }
  },
  computed: {
    ...mapState('MapOptions', ['point', 'city']),
  },
  methods: {
    ...mapActions('MapOptions', {getIpAddress: 'getIpAddress'}),
    changeState() {
      if (this.state1) {
        this.state1 = !this.state1
        this.styleObj.paddingTop = '15px'
        setTimeout(() => {
          this.state2 = !this.state2
        }, 300)
      }else {
        this.state2 = !this.state2
        this.styleObj.paddingTop = '0'
        setTimeout(() => {
          this.state1 = !this.state1
        }, 300)
      }
    },
    locatePoint(points) {
      let temp = []
      for (let i = 0; i < points.length; i++) {
        temp.unshift(new BMapGL.Point(points[i].lng, points[i].lat))
      }
      let view = this.map.getViewport(temp)
      let mapZoom = view.zoom
      let centerPoint = view.center
      this.map.centerAndZoom(centerPoint, mapZoom)
    },
    openRoadCondition() {
      this.trafficOn = !this.trafficOn
      if (this.trafficOn)
        this.map.setTrafficOn()
      else
        this.map.setTrafficOff()
    },
    drawSpots(word) {
      this.map.clearOverlays();
      let local = new BMapGL.LocalSearch(this.map, {
        renderOptions:{map: this.map}
      });
      local.search(word);
    },
    searchComplete(){
      this.map.clearOverlays();
      let transit = new BMapGL.DrivingRoute(this.map, {renderOptions: {
          map: this.map,
          autoViewport: true,
        }
      })
      let start, end, point1, point2
      for (const item of this.destination) {
        if (item.flag === 1) {
          start = new BMapGL.Point(item.location.lng, item.location.lat)
          point1 = {x: item.location.lng, y: item.location.lat}
        }else {
          end = new BMapGL.Point(item.location.lng, item.location.lat)
          point2 = {x: item.location.lng, y: item.location.lat}
        }
      }
      transit.search(start, end)
      //  http://localhost:8080/BMap/
      let url = `/user/get_routes?point1=${point1.y},${point1.x}&point2=${point2.y},${point2.x}`
      this.$bus.$emit('showFooter', true)
      this.$bus.$emit('showBMapFooter')
      this.$bus.$emit('getUrl', url)
      this.checkRoute = true
    },
    showDrawer() {
      this.$bus.$emit('openDrawer')
    },
    stopLoading(state) {
      this.loading = state
    },
    getPath(item) {
      if (this.destination.length === 2)
        for (let i = 0; i < this.destination.length; i++) {
          if (this.destination[i].flag === item.flag)
            this.destination[i] = item
        }
      else
        this.destination.push(item)
      if (this.destination.length === 2) {
        console.log(this.destination)
        this.searchComplete()
      }
    },
    deleteItem(name) {
      console.log(this.destination, name)
      for(let i = 0; i < this.destination.length; i++) {
        if (this.destination[i].name === name){
          this.destination.splice(i, 1)
          break
        }
      }
    },
    locationOn(map) {
      let locationControl = new BMapGL.LocationControl({
        // 
        anchor: BMAP_ANCHOR_TOP_RIGHT,
        // 
        offset: new BMapGL.Size(20, 20)
      });
      // 
      map.addControl(locationControl);
      let point = JSON.parse(localStorage.getItem('point'))
      if (!point) {
        location.reload()
      }
      map.panTo(new BMapGL.Point(point.x, point.y))
    },
    InitialMap(map) {
      map = new BMapGL.Map("container");    // Map
      map.centerAndZoom(new BMapGL.Point(116.404, 39.915), 11);  // ,
      map.enableScrollWheelZoom(true);     //
      this.locationOn(map)
      let scaleCtrl = new BMapGL.ScaleControl();  // 
      map.addControl(scaleCtrl);
      let zoomCtrl = new BMapGL.ZoomControl();  // 
      map.addControl(zoomCtrl);
      this.map = map
    },
    setLocation() {
      this.getIpAddress()
    }
  },
  mounted() {
    let map
    this.setLocation()
    this.InitialMap(map)
    this.$bus.$on('drawSpots', this.drawSpots)
    this.$bus.$on('getPath', this.getPath)
    this.$bus.$on('deleteItem', this.deleteItem)
    this.$bus.$on('stopLoading', this.stopLoading)
    this.$bus.$on('locatePoint', this.locatePoint)
  },
  beforeDestroy() {
    this.$bus.$emit('showFooter', false)
  }
}
</script>

<style scoped>
#container {
  height: 600px;
  overflow: hidden;
  padding-top: 0;
}
.search {
  width: 300px;
  z-index: 999;
  position: absolute;
  top: 105px;
  margin-left: 10px;
  display: flex;
  display: -webkit-flex;
}
:root {
  --animate-duration: 800ms;
  --animate-delay: 0.9s;
}
.route-button {
  margin-top: 5px;
}
.roadConditionBtn {
  position:absolute;
  left: 1390px;
  top: 110px;
  z-index: 999;
}
</style>