<template>
  <div>
    <el-container>
      <el-aside :width="barWidth">
        <NavBar style="overflow: hidden;"/>
      </el-aside>
      <el-container>
        <el-header>
          <Header/>
        </el-header>
        <el-container>
          <el-main style="padding: 2px 1px 5px 5px; margin-top: 20px">
            <keep-alive>
              <router-view></router-view>
            </keep-alive>
          </el-main>
          <el-footer v-show="showFooter">
            <Footer/>
          </el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<style>

.el-aside {
  transition: width 500ms ease;
}

</style>

<script>
import Header from "@/layouts/default/Header";
import NavBar from "@/layouts/default/NavBar";
import Footer from "@/layouts/default/Footer";
import Cookies from "js-cookie";
export default {
  name: 'Home',
  components: {
    Footer,
    NavBar,
    Header,
  },
  data() {
    return {
      barWidth: '203px',
      showFooter: false
    }
  },
  methods: {
    changeSize(state) {
      this.barWidth = state? '65px': '201px'
    }
  },
  mounted() {
    this.$bus.$on('changeSize', this.changeSize)
    this.$bus.$on('showFooter', (state) => {
      this.showFooter = state
    })
  },
  beforeRouteEnter(to, from ,next) {
    if (to.params.auth === 'true' || Cookies.get('id'))
      next()
    else
      next(vm => {
        vm.$router.replace({name: 'login'})
      })
  }
}
</script>
