import Vue from 'vue'
import VueRouter from 'vue-router'
import Map from "@/views/Map";
import Login from "@/views/Login";
import Home from "@/views/Home";
import File from "@/views/File";
import Chat from "@/views/Chat";
import Analyze from "@/views/Analyze"

Vue.use(VueRouter)

const routes = [
  {
    name: 'home',
    path: '/home',
    component: Home,
    children: [
      {
        name: 'map',
        path: 'map',
        component: Map
      },
      {
        name: 'chat',
        path: 'chat',
        component: Chat
      },
      {
        name: 'file',
        path: 'file',
        component: File
      },
      {
        name: 'analyze',
        path: 'analyze',
        component: Analyze
      }
    ]
  },
  {
    name: 'index',
    path: '/',
    redirect: '/login'
  },
  {
    name: 'login',
    path: '/login',
    component: Login
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
