<template>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" style="font-size: 30px">
    <el-form-item label="" prop="account">
      <el-input type="email"  v-model="ruleForm.account" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" prop="nickName">
      <el-input type="text"  v-model="ruleForm.nickName" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" prop="pass">
      <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="" prop="checkPass" v-show="state">
      <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item style="display: flex; margin-left: 20px">
      <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
      <el-button @click="resetForm('ruleForm')"></el-button>
      <el-button circle icon="el-icon-refresh" @click="changeState"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
import qs from "qs";

export default {
  name: 'RegisterForm',
  data() {
    let checkAccount = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      }else if (value.search(/[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/i) === -1) {
        callback(new Error(''))
      }
      callback()
    }
    let passWordPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    }
    let checkPassWordPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('!'));
      } else {
        callback();
      }
    }
    let checkNickName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value.length > 16) {
          callback(new Error(''))
      } else {
        console.log(qs.stringify({'nickName': this.ruleForm.nickName}, {indices: false}))
        //http://localhost:8080/api
        axios.post('/user/check', qs.stringify({'nickName': this.ruleForm.nickName}, {indices: false}))
        .then(
            res => {
              if (res.data.status === 'error') {
                this.$message.error('')
                callback('')
              }else {
                callback()
              }
            }
        )
      }
    }
    return {
      ruleForm: {
        pass: '',
        checkPass: '',
        account: '',
        nickName: ''
      },
      rules: {
        pass: [
          { validator: passWordPass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: checkPassWordPass, trigger: 'blur' }
        ],
        account: [
          { validator: checkAccount, trigger: 'blur' }
        ],
        nickName: [
          { validator: checkNickName, trigger: 'blur'}
        ]
      },
      msg: ''
    };
  },
  props: ['state'],
  methods: {
    open(text, type) {
      if (type !== 'error') {
        this.$message({
          message: text,
          type: type
        })
      }else
        this.$message.error(text)
    },
    checkBox(title, message, showCancelButton, beforeClose) {
      this.$msgbox({
        title,
        message,
        showCancelButton,
        confirmButtonText: '',
        cancelButtonText: '',
        beforeClose
      }).then(action => {
        this.open(...this.msg);
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const h = this.$createElement;
          let message = h('p', null, '')
          let beforeClose = (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '...';
              let aim = this.state === true? 'register': 'login'
              let obj = {
                account: this.ruleForm.account,
                password: this.ruleForm.pass,
                nickName: this.ruleForm.nickName
              }
              // let url = `http://localhost:8080/api/user/${aim}`
              let url = `/user/${aim}`
              console.log(url)
              axios.post(url, qs.stringify(obj, {indices: false})).then(
                  (res) => {
                    let data = res.data
                    console.log(data)
                    this.msg = [data['info'], data['status']]
                    instance.confirmButtonLoading = false
                    done()
                  },
                  (err) => {
                    this.msg = ['', 'error']
                    instance.confirmButtonLoading = false
                    done()
                  }
              )
            } else {
              done();
            }
          }
          this.checkBox('', message, true, beforeClose)
        } else {
          this.open('', 'error')
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    changeState() {
      this.$bus.$emit('switchState')
    }
  }
}
</script>

<style>
.el-form .el-form-item__label {
  font-size: 20px;
}
</style>