<template>
  <div class="l-scrollbar__bar is-vertical">
    <BMap/>
  </div>
</template>

<script>
import BMap from "@/components/BMap";
import Cookies from "js-cookie";

export default {
  name: "Map",
  components: {
    BMap
  },
  beforeRouteEnter(to, from ,next) {
    if (to.params.auth === 'true' || Cookies.get('id'))
      next()
    else
      next(vm => {
        vm.$router.replace({name: 'login'})
      })
  },
  mounted() {
    this.$bus.$emit('showFooter', true)
  }
}

</script>

<style>
.BMap_cpyCtrl{
  display: none;
}
.anchorBL {
  display: none;
}
</style>