<template>
  <div>
    <SwitchDetail style="margin-bottom: 15px"/>
    <el-steps :active="active" align-center>
      <el-step :title="`${index + 1}`" :description="description" v-for="(description, index) in descriptions" :key="index"></el-step>
    </el-steps>
  </div>
</template>

<script>
import SwitchDetail from "@/components/SwitchDetail";
export default {
  name: "Description",
  components: {SwitchDetail},
  data() {
    return {
      descriptions: [],
      active: 1,
      handler: ''
    }
  },
  methods: {
    autoGetNext() {
      this.handler = setInterval(() => {
        this.active++
        if (this.active >= this.descriptions.length)
          clearInterval(this.handler)
      }, 2000)
    }
  },
  mounted() {
    this.$bus.$on('getDescriptions', (descriptionList) => {
      this.descriptions = descriptionList
      this.autoGetNext()
    })
  }
}
</script>

