<template>
  <div :id="id" :style="styleObj">

  </div>
</template>

<script>
import * as echarts from 'echarts'
export default {
  name: "LineShadowChart",
  props: ['name', "text", 'id', 'getData', 'classifier1', 'classifier2'],
  data() {
    return {
      styleObj: {
        width: '500px',
        height: '300px'
      },
    }
  },
  methods: {
    setOption(myChart, data, date) {
      let option = {
        tooltip: {
          trigger: 'axis',
          position: function (pt) {
            return [pt[0], '10%'];
          }
        },
        title: {
          left: 'center',
          text: this.text
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: 'none'
            },
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: date
        },
        yAxis: {
          type: 'value',
          boundaryGap: [0, '100%']
        },
        dataZoom: [
          {
            type: 'inside',
            start: 0,
            end: 10
          },
          {
            start: 0,
            end: 10
          }
        ],
        series: [
          {
            name: this.name,
            type: 'line',
            symbol: 'none',
            sampling: 'lttb',
            itemStyle: {
              color: 'rgb(255, 70, 131)'
            },
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: 'rgb(255, 158, 68)'
                },
                {
                  offset: 1,
                  color: 'rgb(255, 70, 131)'
                }
              ])
            },
            data: data
          }
        ]
      }
      myChart.setOption(option)
    },
    initialChart(data, date) {
      let chartDom = document.getElementById(this.id);
      let myChart = echarts.init(chartDom);
      this.setOption(myChart, data, date)
    }
  },
  mounted() {
    this.$bus.$on('shadowStart', () => {
      this.initialChart(...this.getData(this.classifier1, this.classifier2))
    })
  }
}
</script>

