<template>
  <div id="main" :style="styleObj"></div>
</template>

<script>
import * as echarts from "echarts"

export default {
  name: "LineChart",
  data() {
    return {
      styleObj: {
        width: '1300px',
        height: '400px'
      }
    }
  },
  methods: {
    option(data, myChart) {
      let option;
      const datasetWithFilters = [];
      const seriesList = [];
      const classifier = ["person", "car", "bus", "motorcycle","truck","traffic light"]
      classifier.forEach(function (kind) {
        const datasetId = 'dataset_' + kind.replace(" ", "_")
        datasetWithFilters.push({
          id: datasetId,
          fromDatasetId: 'dataset_raw',
          transform: {
            type: 'filter',
            config: {
              and: [
                { dimension: 'time', gte: 0 },
                { dimension: 'classifier', '=': kind }
              ]
            }
          }
        });
        seriesList.push({
          type: 'line',
          datasetId: datasetId,
          showSymbol: false,
          name: kind,
          endLabel: {
            show: true,
            formatter: function (params) {
              return params.value[1] + ': ' + params.value[2]
            }
          },
          labelLayout: {
            moveOverlap: 'shiftY'
          },
          emphasis: {
            focus: 'series'
          },
          encode: {
            x: 'time',
            y: 'count',
            label: ['classifier', 'count'],
            itemName: 'time',
            tooltip: ['count']
          }
        });
      });
      option = {
        animationDuration: 10000,
        dataset: [
          {
            id: 'dataset_raw',
            source: data
          },
          ...datasetWithFilters
        ],
        title: {
          text: '',
          left: 'center'
        },
        tooltip: {
          order: 'valueDesc',
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          nameLocation: 'middle'
        },
        yAxis: {
          name: 'counts'
        },
        grid: {
          right: 140
        },
        series: seriesList
      }
      myChart.setOption(option)
    },
    initialChart(dataset) {
      const chartDom = document.getElementById('main')
      this.option(dataset, echarts.init(chartDom))
    }
  },
  mounted() {
    this.$bus.$on('sendData', this.initialChart)
  }
}
</script>

<style scoped>
#main {

}
</style>