<template>
  <div>
    <JwChat :taleList="list"
            @enter="bindEnter"
            @clickTalk="talkEvent"
            v-model="inputMsg"
            :toolConfig="tool"
            :quickList="quickList"
            scrollType="scroll"
            width="900px"
            height="640px"
            class="box"
    >
    </JwChat>
    <input type="file" style="display: none" ref="img" accept="image/x-png,image/gif,image/jpeg,image/bmp" @change="uploadImg($event)"></input>
  </div>

</template>


<script>
import Cookies from "js-cookie";
export default {
  name: 'ShowChat',
  data () {
    return {
      inputMsg: '',
      list: [],
      tool: {
        //  ["file", "video", "img", "hongbao", "more", "history"]
        show: ['file', 'history', 'img'],// 
        showEmoji: true, // 
        callback: this.toolEvent
      },
      quickList: [],
      time: new Date(),
      path: '',
      user: JSON.parse(Cookies.get('detail'))
    }
  },
  methods: {
    //http://localhost:5000
    getMsgObj(msg) {
      return {
        "date": this.date,
        "text": { "text": msg },
        "mine": true,
        "name": this.user['nickName'],
        "img": `/static${this.user['avatar']}`
      }
    },
    bindEnter (e) {
      console.log(e)
      const msg = this.inputMsg
      if (!msg) return;
      const msgObj = this.getMsgObj(msg)
      this.list.push(msgObj)
      this.socket.send(JSON.stringify([this.user['no'], msgObj]))
    },
    toolEvent (type, obj) {
      console.log('tools', type, obj)
      if (type === 'img') {
        this.$refs.img.click()
      }
    },
    uploadImg(ev) {
      let file = ev.target.files[0]
      console.log(file)
      let reader = new FileReader();
      //
      reader.readAsArrayBuffer(file);
      //
      reader.onload = (evt) => {
        let binaryString = evt.target.result;
        //
        this.socket.send(binaryString)
      }
    },
    talkEvent (play) {
      console.log(play)
    },
    init: function () {
      if(typeof(WebSocket) === "undefined"){
        alert("socket")
      }else{
        // socket
        this.socket = new WebSocket(this.path)
        // socket
        this.socket.onopen = this.open
        // socket
        this.socket.onerror = this.error
        // socket
        this.socket.onmessage = this.getMessage
      }
    },
    open: function () {
      console.log("socket")
    },
    error: function () {
      console.log("")
    },
    getMessage: function (msg) {
      // console.log(msg.data)
      let data = JSON.parse(msg.data)
      console.log(data.message)
      if (data['status'] === 'first')
        this.list = data.message
      else if(data.message['name'] !== this.user['nickName'] || data.type=== 'pic') {
        this.list.push(data.message)
      }
    },
    close: function () {
      console.log("socket")
    }
  },
  computed: {
    date() {
      return `${this.time.getFullYear()}/${this.time.getMonth()}/${this.time.getDate()} ${this.time.getHours()}:${this.time.getMinutes()}:${this.time.getSeconds()}`
    },
  },
  mounted () {
    //ws://localhost:8080/api/ws/chat/traffic/${this.user['nickName']}/${this.user['no']}
    this.path = `ws://localhost:8080/ws/chat/traffic/${this.user['nickName']}/${this.user['no']}`
    this.init()
    console.log('page mounted')
  },
  beforeDestroy() {
    this.socket.close()
  }
}
</script>

<style>
.box {
  margin-top: 30px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}
</style>