<template>
  <div class="upload">
<!--    http://localhost:8080/api/video/upload_files-->
    <el-upload
        class="upload-demo"
        action="/video/upload_files"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :before-remove="beforeRemove"
        :limit="3"
        :on-exceed="handleExceed"
        :auto-upload="false"
        :file-list="fileList"
        ref="upload"
        :data="account"
        :on-success="handleSuccess"
        multiple
    >
      <el-button size="small" type="primary" slot="trigger"></el-button>
      <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
      <div slot="tip" class="el-upload__tip">3</div>
    </el-upload>
  </div>

</template>

<script>
import Cookies from "js-cookie";
export default {
  data() {
    return {
      fileList: [],
      account: {
        account: JSON.parse(Cookies.get('detail'))['no'],
        location: ''
      }
    }
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${ file.name }`);
    },
    submitUpload() {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputValidator: function (value) {
          if (!value)
            return ''
        },
        // inputErrorMessage: ''
      }).then(({ value }) => {
        this.$message({
          type: 'success',
          message: ': ' + value
        })
        this.account.location = value
        this.$refs.upload.submit()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    handleSuccess(response, file, fileList) {
      console.log(response.data)
      this.$message.success(`${file.name}`)
      this.$bus.$emit('refreshComponent')
    }
  },
  mounted() {

  }
}
</script>

<style>
.upload {
  width: 300px;
  justify-content: center;
}
</style>