<template>
  <div style="text-align: center;line-height: 150px">
    <el-card
        shadow="hover"
        v-for="route in routes"
        :key="Id()" style="margin-bottom: 3px; line-height: 40px"
        v-html="route['instruction']"
        @click.native="transformPoint(route['start_location'], route['end_location'])">
      <div v-if="route['traffic_condition'][0].status >= 3" style="background-color: orange">
        {{route['instruction']}}
      </div>
      <div v-else>
        {{route['instruction']}}
      </div>
    </el-card>
  </div>
</template>

<script>
import {nanoid} from 'nanoid'
export default {
  name: "DisplayCard",
  props: ['duration', 'distance', 'routes'],
  computed: {
  },
  methods: {
    transformPoint(point1, point2) {
      console.log(point1, point2)
      this.$bus.$emit('locatePoint', [point1, point2])
    },
    Id() {
      return nanoid()
    }
  }
}
</script>

