<template>
  <div style="margin-top: 20px;width: 1101px; align-self: center">
    <el-table
        :data="fileList"
        border
        >
      <el-table-column
          fixed
          prop="file_name"
          label=""
          >
      </el-table-column>
      <el-table-column
          prop="location"
          label=""
      >
      </el-table-column>
      <el-table-column
          prop="date"
          label=""
      >
      </el-table-column>
      <el-table-column
        label=""
        prop="operation"
      >
      </el-table-column>
      <el-table-column
          label=""
          width="240px"
          >
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="openPreView(scope.row)"></el-button>
          <el-button type="text" size="small" @click="handleClick(scope.row)" ></el-button>
          <el-button type="text" size="small" @click="removeItem(scope.row)"></el-button>
          <el-button type="text" size="small" @click="handleVideo(scope.row)" v-if="scope.row.state"></el-button>
          <el-button type="text" size="small" @click="checkHandleVideo(scope.row)" v-else></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="50%"
        >
      <iframe :src="href" style="width: 700px; height: 600px; text-align: center"></iframe>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="dialogVisible = false"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
import Cookies from "js-cookie";

export default {
  name: "ShowFiles",
  data() {
    return {
      fileList: [],
      dialogVisible: false,
      href: '',
      msg: '',
      path: '',
      id: ''
    }
  },
  methods: {
    open1(text, type) {
      if (type !== 'error') {
        this.$message({
          message: text,
          type: type
        })
      }else
        this.$message.error(text)
    },
    checkBox(title, message, showCancelButton, beforeClose) {
      this.$msgbox({
        title,
        message,
        showCancelButton,
        confirmButtonText: '',
        cancelButtonText: '',
        beforeClose
      }).then(action => {
        this.$message.success('')
        this.getData()
      })
    },
    handleClick(row) {
      //http://localhost:8080/api/video/download_files?url=./dist/static${row['file']}&name=${row['file_name']}
      window.location.href = `/video/download_files?url=./dist/static${row['file']}&name=${row['file_name']}`
    },
    getData() {
      // http://localhost:8080/api/video/show_files
      axios.post('/video/show_files', qs.stringify({account: JSON.parse(Cookies.get('detail'))['no']}, {indices: false}))
          .then(
              res => {
                this.fileList = res.data['file_list']
                console.log(this.fileList)
              }
          )
    },
    openPreView(row) {
      this.dialogVisible = true
      //http://localhost:8080/api/static${row['file']}
      this.href = `/static${row['file']}`
      console.log(this.href)
    },
    removeItem(row) {
      const h = this.$createElement;
      let message = h('p', null, '')
      let beforeClose = (action, instance, done) => {
        if (action === 'confirm') {
          instance.confirmButtonLoading = true;
          instance.confirmButtonText = '...';
          //http://localhost:8080/api/video/delete_file?id=${row['id']}
          axios.get(`/video/delete_file?id=${row['id']}`).then(
              (res) => {
                let data = res.data
                console.log(data)
                this.msg = data['status']
                instance.confirmButtonLoading = false
                done()
              },
              (err) => {
                this.msg = ['', 'error']
                instance.confirmButtonLoading = false
                done()
              }
          )
        } else {
          done();
        }
      }
      this.checkBox('', message, true, beforeClose)
    },
    init: function () {
      if(typeof(WebSocket) === "undefined"){
        alert("socket")
      }else{
        // socket
        this.socket = new WebSocket(this.path)
        // socket
        this.socket.onopen = this.open
        // socket
        this.socket.onerror = this.error
        // socket
        this.socket.onmessage = this.getMessage
      }
    },
    open: function () {
      console.log("socket")
    },
    error: function () {
      console.log("")
    },
    handleData(data) {
      if (data.state)
      {
        this.$message.success('')
        for (let i = 0; i < this.fileList.length; i++) {
          if(this.fileList[i].id === this.id) {
            this.fileList[i].operation = ''
            this.fileList[i].state = false
            this.fileList[i].detail = data.detail
          }
        }
      }
      else if (data.order === 'first')
        this.$message.warning('')
      else
        this.$message.error('')
    },
    getMessage: function (msg) {
      // console.log(msg.data)
      let data = JSON.parse(msg.data)
      console.log(data)
      this.handleData(data)
      // if (data.state)
      //   {
      //     this.$message.success('')
      //     for (let i = 0; i < this.fileList.length; i++) {
      //       if(this.fileList[i].id === this.id) {
      //         this.fileList[i].operation = ''
      //         this.fileList[i].state = false
      //         this.fileList[i].detail = data.detail
      //       }
      //     }
      //   }
      // else if (data.order === 'first')
      //   this.$message.warning('')
      // else
      //   this.$message.error('')
    },
    close: function () {
      console.log("socket")
    },
    handleVideo(row) {
      this.$message.info('')
      this.socket.send(JSON.stringify({'no': row.id}))
      this.id = row.id
    },
    checkHandleVideo(row) {
      //`http://localhost:8080/api/video/download_files?url=./dist/static${row['detail']['url']}&name=${row['detail']['name']}`
      window.location.href = `/video/download_files?url=./dist/static${row['detail']['url']}&name=${row['detail']['name']}`
    }
  },
  //'ws://localhost:8080/api/ws/video_handler/handle'
  mounted() {
    this.path = 'ws://localhost:8080/ws/video_handler/handle'
    this.init()
    this.getData()
    this.$bus.$on('refreshComponent', this.getData)
  }
}
</script>

