<template>
  <div style="height: 60px">
    <BMapRoutes v-show="show"/>
  </div>
</template>

<script>
import BMapRoutes from "@/components/BMapRoutes";
export default {
  name: "Footer",
  components: {
    BMapRoutes
  },
  data() {
    return {
      show: false
    }
  },
  mounted() {
    this.$bus.$on('showBMapFooter', () => {
      this.show = true
    })
  }
}
</script>

