import axios from "axios";

const state = {
  city: '',
  spotList: [],
  point: '',
  temp: '',
}

const actions = {
  getIpAddress(context) {
    //http://localhost:8080/BMap/location/ip?ak=TdFnlwfseG2vzNxISSeAaaoMLU27h9sI&coor=bd09ll
    axios.get('/user/get_address').then(
      response => {
        console.log(response.data)
        context.commit('getIpAddress', {
          city: response.data.city,
          point: {
            x: response.data.point.x,
            y: response.data.point.y
          }
        })
      },
      error => {
        console.log(error)
      }
    )
  },
  searchSpotInfo(context, value) {
    //http://localhost:8080/BMap/
    let url = `/user/get_spot_info?query=${value[0]}&region=${value[1]}`
    console.log(value[1])
    if (typeof value[0] === "undefined") {
      value[0] = ''
    }
    if (Math.abs( value[0].length - state.temp.length) >= 2) {
      state.temp = value[0]
      axios.get(url)
        .then(
          response => {
            context.commit('searchSpotInfo', response.data.result)
          },
        )
    }

  }
}

const mutations = {
  getIpAddress(state, value) {
    state.city = value.city
    state.point = value.point
    if (state.point !== "") {
      localStorage.setItem('point', JSON.stringify(state.point))
      localStorage.setItem('city', JSON.stringify(state.city))
    }
  },
  searchSpotInfo(state, value) {
    state.spotList = value
}
}



export default {
  namespaced: true,
  state,
  actions,
  mutations
}