<template>
    <div class="bcg">
      <h1 class="title"></h1>
      <el-card class="loginForm" shadow="hover">
        <div slot="header" class="clearfix">
          <span style="font-size: 20px">{{isRegister}}</span>
        </div>
        <transition name="el-zoom-in-top" mode="in-out">
          <RegisterForm style="position:relative;right: 25px" :state="state" v-show="state" />
        </transition>
        <transition name="el-fade-in" mode="out-in">
          <LoginForm style="position:relative;right: 25px" :state="state1&state" v-show="state1"/>
        </transition>
      </el-card>
    </div>

</template>

<script>
import LoginForm from "@/components/LoginForm";
import RegisterForm from "@/components/RegisterForm";
import { ParticlesBg } from "particles-bg-vue";
import Cookies from "js-cookie";
export default {
  name: "Login",
  components: {
    LoginForm,
    ParticlesBg,
    RegisterForm
  },
  data() {
    return {
      state: true,
      state1: false
    }
  },
  computed: {
    isRegister() {
      return this.state? '': ''
    }
  },
  methods: {
    checkLogin() {
      let id = Cookies.get('id')
      if (id) {
        this.$router.replace({name: 'map', params:{auth: 'true'}})
      }
    }
  },
  mounted() {
    this.$bus.$on('switchState', () => {
      if (this.state) {
        this.state = !this.state
        setTimeout(() => {
          this.state1 = !this.state1
        },400)
      }else {
        this.state1 = !this.state1
        setTimeout(() => {
          this.state = !this.state
        }, 500)
      }
    })
    this.checkLogin()
  }
}
</script>

<style scoped>
.loginForm {
  position: absolute;
  width: 400px;
  top: 230px;
  left: 550px;
  text-align: center;
  background-color: rgb(255, 255, 255, 0);
}
.bcg {
  background-image: url("../assets/bgi.png");
  background-size:100% 100%;
  background-repeat:no-repeat;
  height: 725px;

}

.title {
  position: absolute;
  font-family: ,serif;
  font-size: 80px;
  top: 65px;
  left: 600px;
  font-weight: lighter;
}




</style>