<template>
  <div style="height: 60px">
    <el-drawer
        title=""
        :modal="false"
        :visible.sync="state"
        direction="btt"
    >
      <DisplayCard :distance="distance" :duration="duration" :routes="routes"/>
    </el-drawer>
  </div>
</template>

<script>
import axios from "axios";
import DisplayCard from "@/components/DisplayCard";
export default {
  name: "BMapRoutes",
  components: {DisplayCard},
  data() {
    return {
      state: false,
      routes: [],
      duration: 0,
      distance: 0
    }
  },
  methods: {
    getUrl(url) {
      axios.get(url).then(
          (response) => {
            let data = response.data.result
            console.log(data)
            this.duration = data.duration
            this.distance = data.distance
            this.routes = data.steps
            this.$bus.$emit('stopLoading', false)
          },
          (error) => {}
      )
    }
  },
  mounted() {
    this.$bus.$on('openDrawer', () => {
      this.state = true
    })
    this.$bus.$on('getUrl', this.getUrl)
  }
}
</script>

