<template>
  <div class="chart" v-if="show">
    <div>
      <Description/>
    </div>
    <div class="charts">
      <LineChart/>
    </div>
    <div class="charts">
      <LineShadowChart
          name=""
          class="flex-item"
          text=""
          id="main1"
          :get-data="getValue"
          classifier1="car"
          classifier2="motorcycle"
      />
      <LineShadowChart
          name=""
          class="flex-item"
          text=""
          id="main2"
          :get-data="getValue"
          classifier1="bus"
          classifier2="truck"
      />
      <LineShadowChart
          name=""
          class="flex-item"
          text=""
          id="main3"
          :get-data="getValue"
          classifier1="person"
          classifier2="person"
      />
    </div>
  </div>
  <div v-else><el-empty description=""></el-empty></div>
</template>

<script>
import LineChart from "@/components/LineChart";
import qs from "qs";
import Cookies from "js-cookie";
import axios from "axios";
import LineShadowChart from "@/components/LineShadowChart";
import Description from "@/components/Description";
export default {
  name: "Analyze",
  data() {
    return {
      dataset: [],
      value: {},
      results: {},
      show: true
    }
  },
  methods: {
    filter(classifier1, classifier2) {
      let time = [], data = []
      let classifier = ''
      if (classifier1 in this.value && classifier2 in this.value) {
        classifier = this.value[classifier1].length < this.value[classifier2].length? classifier2: classifier1
        let min = Math.min(this.value[classifier1].length, this.value[classifier2].length)
        let max = Math.max(this.value[classifier1].length, this.value[classifier2].length)
        for (let i = 0; i < min; i++) {
          if (this.value[classifier1][i][0] === this.value[classifier2][i][0]) {
            time.push(this.value[classifier1][i][0])
            data.push(this.value[classifier1][i][1] + this.value[classifier2][i][1])
          }else if (this.value[classifier1][i][0] < this.value[classifier2][i][0]) {
            time.push(this.value[classifier1][i][0])
            data.push(this.value[classifier1][i][1])
            time.push(this.value[classifier2][i][0])
            data.push(this.value[classifier2][i][1])
          }else {
            time.push(this.value[classifier2][i][0])
            data.push(this.value[classifier2][i][1])
            time.push(this.value[classifier1][i][0])
            data.push(this.value[classifier1][i][1])
          }
        }
        for (let i = min; i < max; i++) {
          time.push(this.value[classifier][i][0])
          data.push(this.value[classifier][i][1])
        }
      }else {
        classifier1 in this.value? this.value[classifier1].forEach((item) => {
          time.push(item[0])
          data.push(item[1])
        }): this.value[classifier2].forEach((item) => {
          time.push(item[0])
          data.push(item[1])
        })
      }
      return [data, time]
    },
    getValue(classifier1, classifier2) {
      const classifiers = ["person", "car", "bus", "motorcycle","truck","traffic light", 'bicycle']
      for (const classifier of classifiers) {
        this.value[classifier] = []
      }
        for (const data of this.dataset) {
          if (data[1] === 'classifier')
            continue
          this.value[data[1]].push([data[0], data[2]])
        }
      return this.filter(classifier1, classifier2)
    },
    initialData(detail, info) {
      this.$bus.$emit('getDescriptions', info)
      this.$bus.$emit('sendData', detail)
      this.dataset = detail
      this.$bus.$emit('shadowStart')
    }
  },
  components: {
    Description,
    LineShadowChart,
    LineChart
  },
  mounted() {
    //http://localhost:8080/api/video/get_video_detail
    axios.post(
        '/video/get_video_detail',
        qs.stringify({no: JSON.parse(Cookies.get('detail'))['no']}, {indices: false})
    ).then(
        res => {
          if (res.data['detail_list'].length) {
            this.$bus.$emit('getDetailList', res.data.id, res.data['detail_list'])
            this.initialData(res.data.detail, res.data.info)
          }else
            this.show = false
        }
    )
    this.$bus.$on('resetData', this.initialData)
  }
}
</script>

<style scoped>
.charts {
  display: flex;
  margin-top: 30px;
}
.flex-item {
  align-items: center;
  margin-left: 30px;
  flex-flow: column wrap;
  justify-content: center;
}
</style>