<template>
  <div style="display: flex">
    <el-button circle icon="el-icon-location" type="info" @click="toMap"></el-button>
    <el-button circle icon="el-icon-menu" type="info" style="margin-left: 42px" @click="toChat"></el-button>

    <DropDown>
      <el-button circle icon="el-icon-user" type="info" style="margin-left: 42px" v-if="state"></el-button>
      <el-button circle :style="ig"  style="margin-left: 42px" v-else></el-button>

    </DropDown>

  </div>
</template>

<script>
import DropDown from "@/components/DropDown";
import Cookies from "js-cookie";
import axios from "axios";
export default {
  name: "HeaderRight",
  data() {
    return {
      url: '',
      state: false
    }
  },
  components: {
    DropDown
  },
  computed: {
    ig() {
      return `background-image: url("${this.url}"); height:40px; width:40.8`
    }
  },
  methods: {
    toMap() {
      this.$router.push({name: 'map'})
    },
    toChat() {
      this.$router.push({name: 'chat'})
    }
  },
  mounted() {
    axios.get(`/user/get_avatar?no=${JSON.parse(Cookies.get('detail'))['no']}`).then(
        res => {
          let data = res.data
          console.log(data)
          this.state = data.state
          this.url = data.url
        }
    )
  }

}
</script>

