<template>
  <div style="margin: 0; padding: 0">
    <el-dropdown size="mini">
      <slot></slot>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item @click.native="getPersonInfo">
          
        </el-dropdown-item>
        <el-dropdown-item @click.native="$refs.avatar.click()">
          
        </el-dropdown-item>
        <hr>
        <el-dropdown-item @click.native="quit">
          
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-dialog title="" :visible.sync="dialogTableVisible">
      <el-descriptions title="">
        <el-descriptions-item :label="key" v-for="(key, index) in Object.keys(personList)" :key="index">{{personList[key]}}</el-descriptions-item>
      </el-descriptions>
    </el-dialog>
    <input type="file" accept="image/x-png,image/gif,image/jpeg,image/bmp" style="display: none" ref="avatar" @change="uploadAvatar($event)"/>
  </div>

</template>

<script>
import Cookies from "js-cookie";
import axios from "axios";
import qs from "qs";
export default {
  name: "DropDown",
  data() {
    return {
      dialogTableVisible: false,
      personList: []
    }
  },
  methods: {
    open(text, type) {
      if (type !== 'error') {
        this.$message({
          message: text,
          type: type
        })
      }else
        this.$message.error(text)
    },
    checkBox(title, message, showCancelButton, beforeClose) {
      this.$msgbox({
        title,
        message,
        showCancelButton,
        confirmButtonText: '',
        cancelButtonText: '',
        beforeClose
      }).then(action => {
        this.open('', 'success')
        this.$router.replace('/login')
      })
    },
    quit() {
      const h = this.$createElement;
      let message = h('p', null, '')
      let beforeClose = (action, instance, done) => {
        if (action === 'confirm') {
          Cookies.remove('id')
          Cookies.remove('detail')
          instance.confirmButtonLoading = true
          instance.confirmButtonText = '...';
          done()
          instance.confirmButtonLoading = false
        } else {
          done();
        }
      }
      this.checkBox('', message, true, beforeClose)
    },
    getPersonInfo() {
      //http://localhost:8080/api
      axios.post('/user/get_personal_info', qs.stringify({
        'no': JSON.parse(Cookies.get('detail'))['no']}, {indices: false})).then(
            res => {
              this.personList = res.data
              this.dialogTableVisible = true
              console.log(res.data)
            }
      )
    },
    uploadAvatar(e) {
      let file = e.target.files[0];
      let param = new FormData(); //form
      param.append('img',file);//appendform
      param.append('no', JSON.parse(Cookies.get('detail'))['no'])
      console.log(param.get('video')); //FormDataget
      let config = {
        headers:{'Content-Type':'multipart/form-data'}
      }
      //http://localhost:8080/api
      axios.post("/user/upload_avatar", param, config).then(
          res => {
            if (res.data.status === 'ok')
              this.$message.success('')
            else
              this.$message.error('')
          }
      )
    }
  }
}
</script>

