<template>
  <div>
    <el-button type="primary" @click="dialogTableVisible = true" plain></el-button>
    <div style="text-align: center">
      <el-dialog title="" :visible.sync="dialogTableVisible">
        <el-table :data="detailList">
          <el-table-column
              prop="title"
              label=""></el-table-column>
          <el-table-column
              prop="location"
              label=""></el-table-column>
          <el-table-column
              prop="date"
              label=""></el-table-column>
          <el-table-column
              label="">
            <template slot-scope="scope">
              <el-button type="success" icon="el-icon-check" plain size="small" circle v-if="scope.row.id === id" ></el-button>
              <el-button type="warning" icon="el-icon-check" size="small" circle plain v-else @click="selectDetail(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>

      </el-dialog>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";

export default {
  name: "SwitchDetail",
  data() {
    return {
      dialogTableVisible: false,
      detailList: [],
      id: ''
    }
  },
  methods: {
    selectDetail(row) {
      this.id = row.id
      //http://localhost:8080/api
      axios.post(
          '/video/select_video_detail',
          qs.stringify({no: row.id}, {indices: false})
      ).then(
          res => {
            this.$bus.$emit('resetData', res.data.detail, res.data.info)
          }
      )
    }
  },
  mounted() {
    this.$bus.$on('getDetailList', (id, list) => {
      this.detailList = list
      this.id = id
    })
  }
}
</script>

